import React, { Component, PropTypes } from "react";

import { color } from "../../../vars";

import HeroFeature from '../../../components/HeroFeature'
import Container from "../../../components/Container";
import Column from "../../../components/Column";
import BlockGrid from "../../../components/BlockGridWhite";


class UI extends Component {
    componentWillMount() {
        this.state = {
            theme: {
                primary: color.express,
                secondary: color.white
            }
        }
        document.body.style.backgroundColor = this.state.theme.primary
        document.body.style.color = this.state.theme.secondary
    }

    play() {
        const audio = document.getElementById('audio')
        audio.play()
    }

    render() {
        const involvement = [
            { name: 'Workflows + product mapping' },
            { name: 'Wireframing' },
            { name: 'Art + film direction' },
            { name: 'Lead design' },
            { name: 'Illustration' },
            { name: 'Lead frontend development' },
            { name: 'Responsive webapp design' }
        ]
        const processDiscover = [
            { name: 'Client workshops' },
            { name: 'Current market alternatives' }
        ]
        const processAnalyse = [
            { name: 'Team workshop' },
            { name: 'Product map' },
            { name: 'Concepts' }
        ]
        const processDevelop = [
            { name: 'Wireframes' },
            { name: 'Hi-fi designs' },
            { name: 'Prototypes + MVP' },
            { name: 'Develop product with continuous releases' }
        ]
        const processOutcome = [
            { name: 'Product released' },
            { name: 'Handover + training' }
        ]

        return (
            <div>
                <HeroFeature headline='UI Design' image='ux' color={this.state.theme.primary} />
                <Container>
                    <Column width="full">
                        <BlockGrid
                            title=""
                            client="Chain Tech"
                            url="/work/ui/chain"
                            path="chain"
                        />
                        <BlockGrid
                            title=""
                            client="RainMaker"
                            url="/work/ui/rainmaker"
                            path="rainmaker"
                        />
                    </Column>
                </Container>
            </div>
        );
    }
}

export default UI;



