import React, { Component, PropTypes } from "react";

import { color } from "../../vars";

import Hero from "../../components/Hero";
import Container from "../../components/Container";
import Column from "../../components/Column";
import BlockFeature from "../../components/BlockFeature";
import ListItem from "../../components/ListItem";
import AnchorLink from "../../components/AnchorLink";
import ClientLogo from "../../components/ClientLogo";
import Media from "../../components/Media";
import Button from "../../components/Button";
import IntroContent from "../Publications/intro.md";

class Home extends Component {
  componentWillMount() {
    this.state = {
      theme: {
        primary: color.xr,
        secondary: color.white,
      },
    };
    document.body.style.backgroundColor = this.state.theme.primary;
    document.body.style.color = this.state.theme.secondary;
  }

  render() {
    return (
      <div>
        <Hero
          headline="Human-Computer Interaction and Machine Learning."
        />
        <hr />
      <Container>
          <Column width="full" headline="News">
              <ListItem
                  title="09/2022 | NeurIPS 2022, including a Spotlight paper"
                  summary="I have one Spotlight paper and two Workshop papers accepted by NeurIPS 2022! Thanks my coauthors."
                  link=""
              />
              <ListItem
                  title="12/2021 | IUI 2022"
                  summary="My first-author paper titled 'Interpretable Aesthetic Analysis Model for Intelligent Photography Guidance Systems' has been accepted to IUI 2022 (27th Annual Conference on Intelligent User Interfaces)"
                  link=""
              />
          </Column>
      </Container>
          <hr />
        <Container>
            <Column width="twoTenth" headline="Research Overview">
            </Column>
          <Column width="sixTenth">
              <Media
                  media='image'
                  url='../images/overview.png'
                  type='media'
                  gradient='false'
                  color={this.state.theme.primary} />
          </Column>
            <Column width="twoTenth">
            </Column>
        </Container>
      </div>
    );
  }
}

export default Home;



